import React, { useEffect, useState } from 'react';
import { MapContainer, TileLayer, Marker, Polyline, useMap } from 'react-leaflet';
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import './MapView.css';

const vehicleData = [
  { 
    id: 1, 
    position: [37.5658, 126.9792], 
    info: '차량 1: 상세 정보', 
    time: '2024-08-01 10:00', 
    description: '과속 사건',
    path: [
      [37.5741, 126.9807],  // 시작 지점
      [37.5733, 126.9814], 
      [37.5727, 126.9822], 
      [37.5711, 126.9837], 
      [37.5689, 126.9858], 
      [37.5662, 126.9888], 
      [37.5658, 126.9790], 
      [37.5658, 126.9792]   // 사고 지점
    ]
  }
];

const getCustomIcon = (description: string) => {
  let iconUrl = '/icons/default_icon.png'; 

  if (description.includes('사고 다발지역')) {
    iconUrl = '/icons/accident_icon.png';
  } else if (description.includes('과속 사건')) {
    iconUrl = '/icons/speeding_icon.png';
  } else if (description.includes('주차 위반')) {
    iconUrl = '/icons/parking_violation_icon.png';
  }

  return new L.Icon({
    iconUrl,
    iconSize: [50, 50],
    iconAnchor: [25, 50], 
  });
};

const SetViewOnLoad: React.FC<{ coords: L.LatLngExpression, zoom: number }> = ({ coords, zoom }) => {
  const map = useMap();
  useEffect(() => {
    map.setView(coords, zoom);
  }, [map, coords, zoom]);
  return null;
};

const MapView: React.FC = () => {
  const [searchQuery, setSearchQuery] = useState('');
  const [filteredData, setFilteredData] = useState(vehicleData);

  const handleSearch = () => {
    if (searchQuery.trim() === '') {
      setFilteredData(vehicleData);
    } else {
      const queryLowerCase = searchQuery.toLowerCase();
      setFilteredData(
        vehicleData.filter((vehicle) =>
          vehicle.info.toLowerCase().includes(queryLowerCase) ||
          vehicle.description.toLowerCase().includes(queryLowerCase) ||
          vehicle.time.includes(queryLowerCase)
        )
      );
    }
  };

  // 기본 위치 설정 (예: 서울)
  const defaultCoords: L.LatLngExpression = [37.5665, 126.9780];
  const defaultZoom = 15;

  return (
    <div className="map-view-container">
      <div className="map-view-header">
        <div className="search-bar">
          <input
            type="text"
            placeholder="차량 정보, 시간 또는 설명으로 검색"
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            onKeyPress={(e) => e.key === 'Enter' && handleSearch()} 
          />
          <button onClick={handleSearch}>검색</button>
        </div>
      </div>
      <div className="map-view-content">
        <div className="map-view-map">
          <MapContainer style={{ height: "80vh", width: "100%" }}>
            <SetViewOnLoad 
              coords={filteredData.length > 0 ? filteredData[0].position as L.LatLngExpression : defaultCoords} 
              zoom={defaultZoom} 
            />
            <TileLayer
              url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
            />
            {filteredData.map((vehicle) => (
              <>
                <Polyline 
                  key={`path-${vehicle.id}`} 
                  positions={vehicle.path as L.LatLngExpression[]} 
                  color="blue"
                  weight={4}
                  dashArray="4 12" // 점선 설정
                />
                <Marker
                  key={vehicle.id}
                  position={vehicle.position as L.LatLngExpression}
                  icon={getCustomIcon(vehicle.description) as any}
                />
              </>
            ))}
          </MapContainer>
        </div>
        <div className="map-view-sidebar">
          <h2>검색 결과</h2>
          <ul className="map-view-results">
            {filteredData.length > 0 ? (
              filteredData.map((vehicle) => (
                <li key={vehicle.id}>
                  <strong>{vehicle.info}</strong>
                  <p>{vehicle.time}</p>
                  <p>{vehicle.description}</p>
                </li>
              ))
            ) : (
              <p>검색 결과가 없습니다.</p>
            )}
          </ul>
        </div>
      </div>
    </div>
  );
};

export default MapView;
